import React, { useRef } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  TableContainer,
  TableHead,
  Paper,
  Link,
  Button,
  Box,
} from "@mui/material";

const ProductCard = ({ product }) => {
  const containerRef = useRef(null);
  const bottomRef = useRef(null);

  const scrollToBottom = () => {
    // scroll the inner scrollable container to bottom
    const el = containerRef.current;
    if (!el) return;
    // el is the TableContainer DOM node (Paper). The scrollable element is itself (overflowY:auto).
    el.scrollTo({ top: el.scrollHeight, behavior: "smooth" });
    // also ensure bottomRef in view (defensive)
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const fields = [
    { label: "Title", value: product.title },
    { label: "ASIN", value: product.asin },
    { label: "Price", value: product.wholePriceBlockText || "N/A" },
    { label: "Availability", value: product.availability || "N/A" },
    {
      label: "Avg. Customer Reviews",
      value: product.avgCustomerReviews || "N/A",
    },
    { label: "Ratings Count", value: product.ratingsCount || "N/A" },
    { label: "Merchant Info", value: product.merchantInfo || "N/A" },
    {
      label: "Store Link",
      value: product.storeLinkText ? (
        <Link href={product.url} target="_blank" underline="hover">
          {product.storeLinkText}
        </Link>
      ) : (
        "N/A"
      ),
    },
    {
      label: "Product Description",
      value: product.productDescription || "N/A",
    },
    { label: "Additional Info", value: product.additionalInfo || "N/A" },
    { label: "Product Overview", value: product.productOverview || "N/A" },
    {
      label: "Product Information",
      value: product.productInformation || "N/A",
    },
    { label: "Tech Specs", value: product.techSpec || "N/A" },
    { label: "Product Details", value: product.productDetails || "N/A" },
    { label: "About Product", value: product.aboutProduct || "N/A" },
  ];

  return (
    <Box sx={{ position: "relative" }}>
      {/* Table container - keep ref to scroll */}
      <TableContainer
        component={Paper}
        ref={containerRef}
        sx={{
          maxHeight: 350,
          overflowY: "auto",
          borderRadius: "10px",
          border: "1px solid #e0e0e0",
          // scrollbar styling
          scrollbarWidth: "thin",
          "&::-webkit-scrollbar": { width: "6px" },
          "&::-webkit-scrollbar-thumb": {
            backgroundColor: "#90caf9",
            borderRadius: "10px",
          },
          "&::-webkit-scrollbar-thumb:hover": { backgroundColor: "#42a5f5" },
        }}
      >
        <Table size="small" stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell
                colSpan={2}
                sx={{
                  fontSize: "0.9rem",
                  fontWeight: "bold",
                  backgroundColor: "#0D47A1",
                  color: "white",
                  textAlign: "center",
                }}
              >
                Product Information
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {fields.map((row) => (
              <TableRow
                key={row.label}
                sx={{
                  "&:nth-of-type(odd)": { backgroundColor: "#f7f9fc" },
                  "&:hover": { backgroundColor: "#e3f2fd" },
                  transition: "0.15s",
                }}
              >
                <TableCell
                  sx={{
                    fontWeight: "bold",
                    width: "35%",
                    borderRight: "1px solid #ddd",
                    color: "#1565c0",
                    padding: "10px",
                  }}
                >
                  {row.label}
                </TableCell>

                <TableCell sx={{ padding: "10px", wordBreak: "break-word" }}>
                  {row.value}
                </TableCell>
              </TableRow>
            ))}

            {/* bottom spacer row with ref target */}
            <TableRow>
              <TableCell colSpan={2} sx={{ p: 0, border: "none" }}>
                <div ref={bottomRef} />
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>

      {/* Overlayed Scroll button  always visible within container viewport */}
      <Button
        onClick={scrollToBottom}
        variant="contained"
        size="small"
        sx={{
          position: "absolute",
          right: 12,
          bottom: 12,
          zIndex: 10,
          textTransform: "none",
          bgcolor: "rgba(13,71,161,0.95)",
          color: "#fff",
          fontWeight: 700,
          boxShadow: "0 4px 10px rgba(0,0,0,0.12)",
          "&:hover": { bgcolor: "rgba(13,71,161,1)" },
          minWidth: "72px",
          px: 1.2,
        }}
      >
        Scroll 
      </Button>
    </Box>
  );
};

export default ProductCard;
