/* global chrome */
import React, { useState, useEffect } from "react";
import { Chip } from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import {
  Card,
  CardContent,
  Typography,
  Button,
  Snackbar,
  Alert,
  Stack,
  Badge,
} from "@mui/material";
import UpgradeModal from "./UpgradeModal";
import ProductCard from "./ProductCard";

export default function AmazonProductScraper() {
  const [product, setProduct] = useState(null);
  const [exportList, setExportList] = useState([]);
  const [successMsg, setSuccessMsg] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [isPaid, setIsPaid] = useState(false);
  const [isAdded, setIsAdded] = useState(false);
  const [isAmazonSite, setIsAmazonSite] = useState(true);
  const [isProductPage, setIsProductPage] = useState(true);

  const FREE_LIMIT = 5;

  // Utility to detect current Amazon marketplace key
  const getMarketplaceKey = (url) => {
    try {
      const hostname = new URL(url).hostname;
      const match = hostname.match(/amazon\.[a-z.]+$/);
      return match ? match[0] : "amazon.com";
    } catch (err) {
      return "amazon.com";
    }
  };

  // Validate license on launch
  useEffect(() => {
    const checkLicense = async () => {
      const storage = await chrome.storage.local.get("gumroadLicenseKey");
      const license = storage.gumroadLicenseKey;

      if (!license) {
        setIsPaid(false);
        return;
      }

      try {
        const response = await fetch(
          "https://api.gumroad.com/v2/licenses/verify",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              product_id: "UhZmvPhcwxwGkMb9S3dW5A==",
              license_key: license,
            }),
          }
        );

        if (response.status === 200) {
          const data = await response.json();

          if (data.success && data.purchase) {
            const p = data.purchase;

            // Determine if user is actively paying
            const active =
              !p.refunded &&
              !p.disputed &&
              !(p.subscription_ended_at || p.subscription_failed_at);

            setIsPaid(active);
          } else {
            setIsPaid(false);
          }
        } else {
          setIsPaid(false);
        }
      } catch (err) {
        console.error("License check failed", err);
        setIsPaid(false);
      }
    };

    checkLicense();
  }, []);

  // Fetch existing export list for current marketplace
  useEffect(() => {
    const fetchExportList = async () => {
      const [tab] = await chrome.tabs.query({
        active: true,
        currentWindow: true,
      });
      const marketplaceKey = getMarketplaceKey(tab.url);
      const storageData = await chrome.storage.local.get(marketplaceKey);
      setExportList(storageData[marketplaceKey] || []);
    };
    fetchExportList();
  }, []);

  // Scrape product when popup opens
  useEffect(() => {
    scrapeCurrentProduct();
  }, []);

  // Update button state if product is already in export list
  useEffect(() => {
    if (product && exportList.length > 0) {
      const alreadyAdded = exportList.some(
        (item) => item.asin === product.asin
      );
      setIsAdded(alreadyAdded);
    } else {
      setIsAdded(false);
    }
  }, [product, exportList]);

  // Scrape product from current tab
  const scrapeCurrentProduct = async () => {
    try {
      const [tab] = await chrome.tabs.query({
        active: true,
        currentWindow: true,
      });

      const url = tab.url || "";
      console.log("Current URL:", url);

      const isAmazon = /amazon\.[a-z.]+/i.test(url);
      // Check for /dp/, /gp/product/, or pd_rd_i parameter (for variant products)
      const productRegex = /\/(dp|gp\/product)\/([A-Z0-9]{10})/i;
      const variantRegex = /[?&]pd_rd_i=([A-Z0-9]{10})/i;
      const isValidProductPage = isAmazon && (productRegex.test(url) || variantRegex.test(url));

      console.log("Is Amazon:", isAmazon, "Is Valid Product Page:", isValidProductPage);

      setIsAmazonSite(isAmazon);
      setIsProductPage(isValidProductPage);

      if (!isValidProductPage) {
        setProduct(null);
        return;
      }

      const html_content = await chrome.scripting.executeScript({
        target: { tabId: tab.id },
        func: () => {
          // Inline scraping function (can't import modules in executeScript)
          function getActivePrice() {
            const selectors = [
              ".priceToPay",
              "#corePrice_desktop",
              "#corePriceDisplay_desktop_feature_div",
              "li .a-button-selected .twister_swatch_price .olpWrapper",
              ".a-price .a-offscreen",
              "#priceblock_ourprice",
              "#priceblock_dealprice",
              ".a-price-whole"
            ];

            for (const sel of selectors) {
              const el = document.querySelector(sel);
              if (el && el.textContent.trim()) {
                return el.textContent.trim();
              }
            }
            return "";
          }

          function getText(selector) {
            const el = document.querySelector(selector);
            return el ? el.innerText.trim() : '';
          }

          function getAllText(selector) {
            const elements = document.querySelectorAll(selector);
            return Array.from(elements).map(el => {
              const clone = el.cloneNode(true);
              const scripts = clone.querySelectorAll('script');
              scripts.forEach(script => script.remove());
              return clone.innerText.trim();
            }).filter(text => text.length > 0).join(' | ');
          }

          function getAsin() {
            const url = window.location.href;
            
            let match = url.match(/\/dp\/([A-Z0-9]{10})/i) || url.match(/\/gp\/product\/([A-Z0-9]{10})/i);
            if (match) return match[1];
            
            match = url.match(/[?&]pd_rd_i=([A-Z0-9]{10})/i);
            if (match) return match[1];
            
            const asinElement = document.querySelector('[data-asin]');
            if (asinElement && asinElement.getAttribute('data-asin')) {
              const asin = asinElement.getAttribute('data-asin');
              if (asin && asin.length === 10) return asin;
            }

            const asinInput = document.querySelector("#ASIN");
            if (asinInput && asinInput.value) {
              return asinInput.value;
            }

            return '';
          }

          return {
            title: getText("#productTitle"),
            asin: getAsin(),
            url: window.location.href,
            wholePriceBlockText: getActivePrice(),
            availability: getText("#availability span"),
            avgCustomerReviews: getText('span[data-hook="rating-out-of-text"]') || getText("#acrPopover"),
            ratingsCount: getText('#acrCustomerReviewText'),
            merchantInfo: getText("#merchantInfoFeature_feature_div") || getText("#merchant-info"),
            storeLinkText: getText("#bylineInfo"),
            productDescription: getText("#productDescription"),
            additionalInfo: getText("#important-information") || getText("#aplus"),
            productOverview: getAllText("#productOverview_feature_div tr") || getText("#feature-bullets"),
            productInformation: getAllText("#prodDetails tr") || getText("#productDetails_techSpec_section_1"),
            techSpec: getAllText("#technicalSpecifications_section_1 tr") || getText("#productDetails_detailBullets_sections1"),
            productDetails: getAllText("#detailBullets_feature_div li") || getText("#detailBullets_feature_div, #detailBulletsWrapper_feature_div"),
            aboutProduct: getAllText("#feature-bullets li") || getText("#aplus"),
            timestamp: new Date().toISOString()
          };
        },
      });

      const scrapedData = html_content[0].result;
      console.log("=== DEBUG: Scraped product data ===");
      console.log("Full data object:", scrapedData);
      console.log("Title:", scrapedData?.title);
      console.log("ASIN:", scrapedData?.asin);
      console.log("URL:", scrapedData?.url);
      console.log("Price:", scrapedData?.wholePriceBlockText);
      console.log("Has data?", !!scrapedData);
      console.log("===================================");

      // Be very lenient - set product if we have ANY data
      if (scrapedData && scrapedData.url) {
        console.log(" Setting product data (URL exists)");
        setProduct(scrapedData);
      } else {
        console.warn(" No product data - scrapedData:", scrapedData);
        setProduct(null);
      }
    } catch (err) {
      console.error("Error scraping product:", err);
      setProduct(null);
    }
  };

  // Add product to export list
  const addToExport = async () => {
    if (!product) return;
    const [tab] = await chrome.tabs.query({
      active: true,
      currentWindow: true,
    });
    const marketplaceKey = getMarketplaceKey(tab.url);

    try {
      const storageData = await chrome.storage.local.get(marketplaceKey);
      let currentList = storageData[marketplaceKey] || [];

      if (!isPaid && currentList.length >= FREE_LIMIT) {
        setErrorMsg(
          `Free plan limit reached! Export only ${FREE_LIMIT} products. Upgrade to PRO for unlimited exports or download your data now!`
        );
        return;
      }

      if (currentList.some((item) => item.asin === product.asin)) {
        setErrorMsg("Product already added to export list.");
        setIsAdded(true);
        return;
      }

      currentList.push(product);
      await chrome.storage.local.set({ [marketplaceKey]: currentList });
      setExportList(currentList);
      setSuccessMsg("Product added to export list!");
      setIsAdded(true);
    } catch (err) {
      console.error(err);
      setErrorMsg("Failed to add product to export list.");
    }
  };

  // Export CSV for current marketplace
  const exportCSV = async () => {
    const [tab] = await chrome.tabs.query({
      active: true,
      currentWindow: true,
    });
    const marketplaceKey = getMarketplaceKey(tab.url);

    try {
      const storageData = await chrome.storage.local.get(marketplaceKey);
      const list = storageData[marketplaceKey] || [];
      if (list.length === 0) {
        setErrorMsg("No products to export for this marketplace.");
        return;
      }

      const csv = convertToCSV(list);
      const blob = new Blob([csv], { type: "text/csv" });
      const a = document.createElement("a");
      a.href = URL.createObjectURL(blob);
      a.download = `${marketplaceKey}_data_${Date.now()}.csv`;
      a.click();

      await chrome.storage.local.remove(marketplaceKey);
      setExportList([]);
      setSuccessMsg("Exported products to CSV successfully!");
      setIsAdded(false);
    } catch (err) {
      console.error(err);
      setErrorMsg("Failed to export CSV.");
    }
  };

  // Clear export list for current marketplace
  const clearExport = async () => {
    chrome.storage.local.get(null, (items) => {
      const keysToRemove = Object.keys(items).filter(
        (key) => key.startsWith("amazon") //  adjust based on your naming
      );

      chrome.storage.local.remove(keysToRemove, () => {
        setExportList([]);
        setSuccessMsg("Cleared export list for all marketplaces.");
        setIsAdded(false);
      });
    });
  };

  // Convert array of products to CSV
  const convertToCSV = (arr) => {
    if (!arr || !arr.length) return "";

    const columns = [
      "title",
      "asin",
      "url",
      "wholePriceBlockText",
      "availability",
      "avgCustomerReviews",
      "merchantInfo",
      "storeLinkText",
      "productDescription",
      "additionalInfo",
      "productOverview",
      "productInformation",
      "techSpec",
      "productDetails",
      "aboutProduct",
    ];

    const csvRows = [
      columns.join(","), // header row
      ...arr.map((item) =>
        columns
          .map(
            (col) =>
              `"${
                item[col] !== undefined
                  ? String(item[col]).replace(/"/g, '""')
                  : ""
              }"`
          )
          .join(",")
      ),
    ];

    return csvRows.join("\n");
  };

  return (
    <Stack spacing={2} sx={{ padding: 2, minWidth: 350 }}>
      <Typography
        variant="h4"
        align="center"
        sx={{
          fontWeight: "700",
          color: "primary.main",
          letterSpacing: "0.5px",
          mb: 1,
        }}
      >
        Amazon Product Scraper
      </Typography>

      {product ? (
        <ProductCard product={product} />
      ) : !isAmazonSite ? (
        <Typography
          align="center"
          sx={{ color: "error.main", fontWeight: 600 }}
        >
          This page is not an Amazon product page.
        </Typography>
      ) : (
        <Typography align="center">
          No product data found. Make sure you are on an Amazon product page.
          <br />
          <br />
          For example visit &nbsp;
          <a
            href="https://www.amazon.com/Apple-iPhone-16-Pro-Max/dp/B0DHJ896RY/"
            target="_blank"
            rel="noopener noreferrer"
            style={{ color: "#1976d2", textDecoration: "underline" }}
          >
            iPhone 16 Pro Max product page
          </a>
          &nbsp; and click on the extension icon to see it in action!
        </Typography>
      )}

      {/* First Row: PRO Status or Upgrade Button */}
      {isPaid ? (
        <Button
          variant="contained"
          fullWidth
          sx={{
            background: "linear-gradient(135deg, #ffd700 0%, #ffb700 100%)",
            color: "#333",
            fontWeight: "700",
            fontSize: "16px",
            padding: "12px",
            textTransform: "none",
            "&:hover": {
              background: "linear-gradient(135deg, #ffed4e 0%, #ffd700 100%)",
              boxShadow: "0 4px 12px rgba(255, 215, 0, 0.5)",
            },
          }}
        >
           You are PRO User!
        </Button>
      ) : (
        <UpgradeModal onUpgradeSuccess={() => setIsPaid(true)} />
      )}

      {/* Second Row: Action Buttons */}
      <Stack direction="row" spacing={1} justifyContent="center">
        <Button
          variant="contained"
          onClick={addToExport}
          disabled={isAdded}
          sx={{
            fontWeight: "700",
            width: "180px",
            background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
            color: "#fff",
            textTransform: "none",
            "&:hover": {
              background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
              boxShadow: "0 4px 12px rgba(102, 126, 234, 0.4)",
            },
            "&:disabled": {
              background: "linear-gradient(135deg, #4caf50 0%, #81c784 100%)",
              color: "#fff",
              opacity: 0.9,
            },
          }}
        >
          {isAdded ? " Added" : " Add Product"}
        </Button>

        {exportList.length > 0 && (
          <>
            <Button
              variant="contained"
              onClick={exportCSV}
              sx={{
                position: "relative",
                fontWeight: "700",
                width: "180px",
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                color: "#fff",
                textTransform: "none",
                "&:hover": {
                  background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  boxShadow: "0 4px 12px rgba(102, 126, 234, 0.4)",
                },
              }}
            >
               Export CSV
              <Chip
                label={`${exportList.length}`}
                size="small"
                sx={{
                  position: "absolute",
                  top: -6,
                  right: -6,
                  fontSize: "0.7rem",
                  height: 18,
                  minWidth: 18,
                  bgcolor: "#d32f2f",
                  color: "#fff",
                  fontWeight: 600,
                }}
              />
            </Button>

            <Button
              variant="contained"
              onClick={clearExport}
              sx={{
                fontWeight: "700",
                width: "180px",
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                color: "#fff",
                textTransform: "none",
                "&:hover": {
                  background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  boxShadow: "0 4px 12px rgba(102, 126, 234, 0.4)",
                },
              }}
            >
               Clear List
            </Button>
          </>
        )}
      </Stack>

      {/* Snackbars */}
      <Snackbar
        open={!!successMsg}
        autoHideDuration={2000}
        onClose={() => setSuccessMsg("")}
        anchorOrigin={{ horizontal: "center", vertical: "bottom" }}
      >
        <Alert
          severity="success"
          sx={{
            width: "400px", // custom width
            maxWidth: "none",
            fontSize: "16px",
          }}
        >
          {successMsg}
        </Alert>
      </Snackbar>
      <Snackbar
        open={!!errorMsg}
        autoHideDuration={3000}
        onClose={() => setErrorMsg("")}
        anchorOrigin={{ horizontal: "center", vertical: "bottom" }}
      >
        <Alert
          severity="error"
          sx={{
            width: "400px", // custom width
            maxWidth: "none",
            fontSize: "16px",
          }}
        >
          {errorMsg}
        </Alert>
      </Snackbar>
    </Stack>
  );
}
